import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';

import { Subscription, Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import { PLC, Tool } from 'src/app/shared/models/plc-model';
import { AppState } from 'src/app/store/reducers';
import * as actions from 'src/app/store/actions';
import { ModalComponent } from 'src/app/shared/modules/modal/container/modal.component';
import { TableColumn } from '@sharedModels';
import { ToolStatusType } from 'src/app/shared/enums/tool-status';
import { StopReactionType, ToleranceEnum, WebsocketEnums } from '@sharedEnums';
import { WebsocketService } from 'src/app/shared/websocket/websocket.service';
import { WSPG } from 'src/app/shared/models/ws-pg-model';

@Component({
  selector: 'app-tools',
  templateUrl: './tools.component.html',
})
export class ToolsComponent implements OnInit, OnDestroy {
  plc: PLC;
  store$: Observable<any>;
  storeSubscription: Subscription;
  tool: Tool;
  tools: Tool[];
  table: Tool[];
  plcs: PLC[];
  removeText: string;

  progressStatus: number = 0;

  wsEnums = WebsocketEnums;

  columns: TableColumn[] = [
    {
      name: 'Name',
      sort_by: true,
      descending: true,
      property_name: 'name',
      cell_type: 'text',
    },
    {
      name: 'No. of ref cycles',
      sort_by: false,
      descending: true,
      property_name: 'numberOfReferenceCycles',
      cell_type: 'text',
    },
    {
      name: 'Tool Status',
      sort_by: false,
      descending: true,
      property_name: 'toolStatus',
      cell_type: 'text',
    },
    {
      name: 'Stop reaction',
      sort_by: false,
      descending: true,
      property_name: 'stopReaction',
      cell_type: 'text',
    },
    {
      name: 'Tolerance',
      sort_by: false,
      descending: true,
      property_name: 'tolerance',
      nested_property: 'type',
      cell_type: 'nested-text',
    },
    {
      name: 'Speed',
      sort_by: false,
      descending: true,
      property_name: 'tolerance',
      nested_property: 'speedTolerance',
      cell_type: 'nested-text',
    },
    {
      name: 'Torque',
      sort_by: false,
      descending: true,
      property_name: 'tolerance',
      nested_property: 'torqueTolerance',
      cell_type: 'nested-text',
    },
    {
      name: 'Reference curve calculated',
      sort_by: false,
      descending: true,
      property_name: 'referenceCurveIsCalculated',
      cell_type: 'check-mark',
    },
    {
      name: 'Automatic start of monitoring',
      sort_by: false,
      descending: true,
      property_name: 'automaticMonitoring',
      cell_type: 'check-mark',
    },
    {
      name: 'Actions',
      sort_by: false,
      cell_type: 'action',
      actions: [
        {
          type: 'start',
          icon: 'fa-play',
          text: '',
          title: 'Start monitoring',
        },
        {
          type: 'reference',
          icon: 'fa-object-ungroup',
          text: '',
          title: 'Calculate reference curve',
        },
        {
          type: 'edit',
          icon: 'fa-edit',
          text: '',
          title: 'Edit',
        },
        {
          type: 'delete',
          icon: 'fa-trash-alt',
          text: '',
          title: 'Delete',
        },
      ],
    },
  ];

  @ViewChild('deleteModal') deleteModal: ModalComponent;
  @ViewChild('toolModal') toolModal: ModalComponent;
  @ViewChild('toolEditModal') toolEditModal: ModalComponent;
  @ViewChild('calculationModal') calculationModal: ModalComponent;

  constructor(private store: Store<AppState>, private ws: WebsocketService) {
    this.store$ = this.store;
  }

  ngOnInit(): void {
    this.ws.sub(this.wsEnums.ProgressBar, true).subscribe((message) => {
      const msg: WSPG = message && JSON.parse(message);
      msg &&
        (this.progressStatus =
          Math.round((100 / msg.calculationStatus.total) * msg.calculationStatus.current * 100) /
          100);
    });

    this.storeSubscription = this.store$.subscribe((response: AppState) => {
      response.plcs.plcs.length ? (this.plcs = [...response.plcs.plcs]) : (this.plcs = []);
      response.tools.tools.length
        ? ((this.tools = [...response.tools.tools]),
          response.tools.used && this.usedTool(response.tools.used),
          this.userFriendlyEnums(),
          this.tools.sort((a, b) => a.name.localeCompare(b.name)))
        : (this.tools = []);
      this.plc && this.selectedPLC(this.plc);
    });
  }

  deleteTool() {
    this.store.dispatch(new actions.DeleteTool(this.tool));
  }

  ngOnDestroy(): void {
    this.storeSubscription && this.storeSubscription.unsubscribe();
    this.ws.unsub();
  }

  selectedPLC(plc: PLC): void {
    this.plc = { ...plc };
    this.table = [...this.tools.filter((x) => x.plcId === plc.id)];
  }

  actionTableHandler(event: { type; item }): void {
    this.tool = { ...event.item };

    switch (event.type) {
      case 'edit': {
        this.toolEditModal.open();
        break;
      }
      case 'delete': {
        this.removeText = `Do you want to remove ${event.item.name} of ${this.plc.name} ?`;
        this.deleteModal.open();
        break;
      }
      case 'reference': {
        const copy = { ...this.uppercaseEnums(event.item) };
        copy.calculateReferenceCurve = true;
        this.store.dispatch(new actions.UpdateTool(copy));
        this.calculationModal.open();
        break;
      }
      case 'start': {
        const copy = { ...this.uppercaseEnums(event.item) };
        copy.automaticMonitoring = !copy.automaticMonitoring;
        this.store.dispatch(new actions.UpdateTool(copy));
        break;
      }
      default: {
        break;
      }
    }
  }

  actionModalHandler(item: { data: Tool; edit: boolean }): void {
    item.data = { ...this.uppercaseEnums(item.data) };
    switch (item.edit) {
      case true: {
        this.store.dispatch(new actions.UpdateTool(item.data));
        break;
      }
      case false: {
        this.store.dispatch(new actions.CreateTool(item.data));
        break;
      }
    }
  }

  cancelCalculation(): void {
    const copy = { ...this.uppercaseEnums(this.tool) };
    copy.calculateReferenceCurve = false;
    this.store.dispatch(new actions.UpdateTool(copy));
  }

  private usedTool(data: { id: number; toolNumber: number }): void {
    this.tools = [
      ...this.tools.map((x) => {
        if (x.plcId === data?.id && x.toolNumber === data?.toolNumber) {
          return { ...x, isActive: true };
        } else {
          return { ...x, isActive: false };
        }
      }),
    ];
  }

  // use user readable values of enums
  private userFriendlyEnums(): void {
    for (let i = 0; i < this.tools.length; i++) {
      const copy = { ...this.tools[i] };
      const tolerance = copy?.tolerance && { ...copy?.tolerance };
      copy.stopReaction &&
        (copy.stopReaction = Object.entries(StopReactionType).find(
          (x) => x[0] === this.tools[i].stopReaction
        )[1]);
      copy.toolStatus = Object.entries(ToolStatusType).find(
        (x) => x[0] === this.tools[i].toolStatus
      )[1];
      copy?.tolerance &&
        tolerance?.type &&
        ((tolerance.type = Object.entries(ToleranceEnum).find(
          (x) => x[0] === copy?.tolerance?.type
        )[1]),
        (copy.tolerance = { ...tolerance }));

      this.tools[i] = { ...copy };
    }
  }
  // change enums to BE format
  private uppercaseEnums(object: Tool): Tool {
    const copy = { ...object };
    copy.stopReaction = Object.entries(StopReactionType).find((x) => x[1] === copy.stopReaction)[0];
    copy.toolStatus = Object.entries(ToolStatusType).find((x) => x[1] === copy.toolStatus)[0];
    const tolerance = { ...copy.tolerance };
    tolerance.type = Object.entries(ToleranceEnum).find((x) => x[1] === tolerance.type)[0];
    copy.tolerance = { ...tolerance };
    return copy;
  }
}
