import { Component, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { StopReactionType, ToleranceEnum } from '@sharedEnums';
import { PLC, Tolerance, Tool } from '@sharedModels';
import { ToolStatusType } from 'src/app/shared/enums/tool-status';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'tool-form',
  templateUrl: './tool-form.component.html',
  styles: [
    '.btn{background-color: #2d3748; outline: none}',
    '.btn:hover{background-color: #e2e8f0}',
    'input[type="checkbox"]{box-shadow: none !important; outline: none !important;}',
  ],
})
export class ToolFormComponent {
  // enums Reaction
  reactions = Object.entries(StopReactionType).map((x) => x[1]);
  selectedReaction;
  // enums Tool
  toolTypes = Object.entries(ToolStatusType).map((x) => x[1]);
  selectedTool;
  // enums Tolerance
  toleranceTypes = Object.entries(ToleranceEnum).map((x) => x[1]);
  selectedTolerance;

  // form
  toolForm: FormGroup;

  submitted: boolean;
  plcControl: boolean;
  styleLength: number;
  // _data: PLC;

  @Input() plc: PLC;
  @Input() edit = false;

  @Input() set data(data: Tool) {
    data &&
      (data?.tolerance
        ? this.toolForm.setValue(data)
        : ((data = { ...this.automatic(data) }), this.toolForm.setValue(data)));
    data && this.prettyfyEnums(data);
  }
  @Output() action: EventEmitter<any> = new EventEmitter();
  @Output() cancel: EventEmitter<any> = new EventEmitter();

  constructor(private fb: FormBuilder) {
    this.submitted = false;

    this.toolForm = this.fb.group({
      id: [null],
      plcId: null,
      name: [null, Validators.required],
      toolNumber: [null, Validators.required],
      numberOfReferenceCycles: [null, Validators.required],
      stopReaction: [false, Validators.required],
      automaticMonitoring: [false, Validators.required],
      calculateReferenceCurve: [false, Validators.required],
      referenceCurveIsCalculated: [false, Validators.required],
      toolStatus: [ToolStatusType.MANUALLY_ADDED, Validators.required],
      isActive: false,
      tolerance: this.fb.group({
        type: [null, Validators.required],
        torqueTolerance: [null, Validators.required],
        speedTolerance: [null, Validators.required],
      }),
    });
  }

  onCancel() {
    this.cancel.emit();
  }

  // check for erors in form control
  checkErrors(control: string, tolerance = false): boolean {
    let result: boolean;
    switch (tolerance) {
      case true: {
        const node = this.toolForm.get('tolerance').get(control);
        (node.dirty || (this.submitted && !node.dirty)) && !node.valid
          ? (result = true)
          : (result = false);
        return result;
      }
      case false: {
        const node = this.toolForm.get(control);
        (node.dirty || (this.submitted && !node.dirty)) && !node.valid
          ? (result = true)
          : (result = false);
        return result;
      }
    }
  }

  submit() {
    this.submitted = true;
    this.toolForm.valid &&
      (this.toolForm.patchValue({ plcId: this.plc.id }),
      this.action.emit({ data: this.toolForm.value, edit: this.edit }));
  }

  selected(value: any, type: 'Tool' | 'Reaction' | 'Tolerance'): void {
    switch (type) {
      case 'Tool': {
        this.selectedTool = value;
        this.toolForm.patchValue({ toolStatus: value });
        break;
      }
      case 'Reaction': {
        this.selectedReaction = value;
        this.toolForm.patchValue({ stopReaction: value });
        break;
      }
      case 'Tolerance': {
        this.selectedTolerance = value;
        this.toolForm.patchValue({ tolerance: { type: value } });
        break;
      }
    }
  }

  private automatic(data): Tool {
    data.tolerance = new Tolerance(null, null, null);
    data.numberOfReferenceCycles = null;
    data.stopReaction = StopReactionType.DO_NOTHING;
    return data;
  }

  private prettyfyEnums(data: Tool): void {
    const tolerance = data?.tolerance;
    tolerance.type &&
      (this.selectedTolerance = Object.entries(ToleranceEnum).find(
        (x) => x[1] === data.tolerance.type
      )[1]);

    data.stopReaction &&
      (this.selectedReaction = Object.entries(StopReactionType).find(
        (x) => x[1] === data.stopReaction
      )[1]);

    data.toolStatus &&
      (this.selectedTool = Object.entries(ToolStatusType).find((x) => x[1] === data.toolStatus)[1]);
  }
}
